<template>
  <div class="hello">
    <h1>my dear dear AGNI</h1>
    <h2>Hello {{ user }}!</h2>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      user: "",
    };
  },
  async created() {
    let response = await fetch("/info", {
      method: "GET",
      credentials: "include",
      mode: "cors",
      headers: {
        "Content-Type": "applications/json",
        Authorization: "Bearer " + this.getLtik(),
      },
    });
    let result = await response.json();
    console.log(result);
    this.user = result.name;
  },

  methods: {
    getLtik() {
      const searchParams = new URLSearchParams(window.location.search);
      const ltik = searchParams.get("ltik");
      if (!ltik) {
        alert("Missing lti key.");
      } else {
        return ltik;
      }
    },
  },
};
</script>

